<template>
    <div>
      <div style="height:calc(100vh - 70px);">
        <SeeksRelationGraph
          ref="seeksRelationGraph"
          :options="graphOptions"
          :on-node-click="onNodeClick"
          :on-line-click="onLineClick">
          
        </SeeksRelationGraph>
      </div>
