    </div>
</template>

<script>
import SeeksRelationGraph from 'relation-graph'
export default {
// name: 'Demo',
components: { SeeksRelationGraph },
data() {
    return {
    isShowCodePanel: false,
    graphOptions: {
        allowSwitchLineShape: true,
        allowSwitchJunctionPoint: true,
        defaultJunctionPoint: 'border'
        // "Graph "
    },
    graphJsonData:{
        rootId: 'a',
        nodes: this.getnodes(),
        // [
        //     { id: 'a', text: 'A', nodeShape: 0, width: 150, height: 150},
        //     { id: 'b', text: 'B'},
        //     { id: 'c', text: 'C'},
        //     { id: 'd', text: 'D'},
        //     { id: 'e', text: 'E'},
        //     { id: 'f', text: 'F',color: '#666666',borderColor: '#a6a6a6'}
        // ],
        links: this.getlinks()
        // [
        //     { from: 'a', to: 'b', text: '200', color: '#43a2f1' },
        //     { from: 'a', to: 'c', text: '300', color:'#FF0000' },
        //     { from: 'a', to: 'e', text: '400', color:'#00ff00' },
        //     { from: 'a', to: 'd', text: '500' },
        //     { from: 'a', to: 'f', text: '600' },
        // ]
    },
    timer: null,
    }
},
watch: {
